import React, { useState } from 'react';
import { clsx } from 'clsx';
import styles from './Checkout.module.scss';
import Button, { ButtonSizes, ButtonTypes, ButtonVariables } from '../../elements/Button';
import Input from '../../elements/Input';
import { toast } from 'react-toastify';
import { clearCart } from '../../../store';
import { useNavigate } from 'react-router-dom';

interface CheckoutProps {
  total: number;
}

function Checkout({ total }: CheckoutProps) {
  const navigate = useNavigate();
  const [name, setName] = useState<string>('');
  const [phone, setPhone] = useState<string>('');
  const [address, setAddress] = useState<string>('');

  const checkoutHandler = () => {
    const toastMessage = (
      <div>
        <div> !</div>
        <div> : {name}</div>
        <div>: {phone}</div>
        <div>  {address}</div>
        <Button
          text="     "
          type={ButtonTypes.Text}
          variable={ButtonVariables.Secondary}
          onClick={() => {
            setName('');
            setPhone('');
            setAddress('');
            clearCart();
            navigate('/');
          }}
        />
      </div>
    );
    toast.success(toastMessage);
  };

  return (
    <div className={clsx(styles.Wrapper)}>
      <div className={clsx(styles.Title)}> </div>
      <div className={clsx(styles.Form)}>
        <Input
          value={name}
          name="name"
          type="text"
          placeholder=" "
          onChange={(val) => setName(val)}
        />
        <Input
          value={phone}
          name="phone"
          type="text"
          placeholder="+ 7 904 000 80 80"
          onChange={(val) => setPhone(val)}
        />
        <Input
          value={address}
          name="address"
          type="text"
          placeholder=" "
          onChange={(val) => setAddress(val)}
        />
      </div>
      <div className={clsx(styles.Total)}>
        <span>: </span>
        <span>{total} .</span>
      </div>
      <Button
        text=" "
        variable={ButtonVariables.Secondary}
        size={ButtonSizes.Large}
        type={ButtonTypes.Button}
        fullWidth
        onClick={checkoutHandler}
      />
    </div>
  );
}

export default Checkout;
